import AanbodInfo from '@/components/Aanbod/Aanbod-info'
import Pakketten from '@/components/Aanbod/Pakketten'
import PakkettenInfo from '@/components/Aanbod/Pakketten-info'
export default {
  components:{
    AanbodInfo,
    Pakketten,
    PakkettenInfo
  },
  data () {
   return {
     story: { content: {} }
   }
 },
 mounted () {
   this.$storybridge(() => {
     const storyblokInstance = new StoryblokBridge()
     storyblokInstance.on('input', (event) => {
       console.log(this.story.content)
       if (event.story.id === this.story.id) {
         this.story.content = event.story.content
       }
     })
     storyblokInstance.on(['published', 'change'], (event) => {
       this.$nuxt.$router.go({
         path: this.$nuxt.$router.currentRoute,
         force: true,
       })
     })
   })
 },
 asyncData (context) {
   return context.app.$storyapi.get('cdn/stories/aanbod', {
     version: context.isDev ? 'draft' : 'published'
   }).then((res) => {
     return res.data
   }).catch((res) => {
     if (!res.response) {
       console.error(res)
       context.error({ statusCode: 404, message: 'Failed to receive content form api' })
     } else {
       console.error(res.response.data)
       context.error({ statusCode: res.response.status, message: res.response.data })
     }
    })
  }
}
